.header {
    display: flex;
    justify-content: space-between;
    background: linear-gradient(black, rgba(0, 120, 120, 0.5));

    .header__link {
        z-index: 1;
        width: 32%;
        display: flex;
        height: 40px;

        &.active {
            background-color: black;
            span {
                color: #8ff;
            }    
        }
        span {
            display: block;
            margin: auto;
            font-size: 2em;
            margin-top: -9px;            
            color:rgb(0, 120, 120);
        }
    }
    :nth-child(2) span {
        font-size: 2.5em;
        margin-top: -12px;
    }
    :nth-child(3) span {
        font-size: 1.8em;
        margin-top: 0px;
    }
}