.current-card {
    max-height: 100vw;
    //background-color:rgb(120, 100, 120);
    position: relative;
    left: 0;
    transition: left 0.2s ease-in;
    &.slideIn {
        left: 100%;
    }

    &__item {
        background-color:black;
        width: 100%;
        height: 100%;
        overflow: hidden;
        position: relative;
    }
    
    //&__de, &__rus, &__eng, &__info {
    &__de, &__info {
        word-wrap: break-word;
        background-image:
        linear-gradient(#555 1px, transparent 1px),
        linear-gradient(90deg, #555 1px, transparent 1px);
        background-size: 2em 2em;
        background-color:dimgray; 
        background-position: 0 0.7em;
        line-height: 2em;	
        color:midnightblue;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        transform: rotateY(90deg);
        transition: transform 0.15s ease 0s;
        display: flex;
        flex-direction: column;
        div {
            margin: auto;
            font-size: 2em;
        }
        &.active {
            transform: rotateY(0deg);
            transition: transform 0.5s ease 0.15s;
        }
    }
    &__de {
        .der {
            color: #00a;
        }
        .das {
            color: #0a0;
        }
        .die {
            color: #a00;
        }
    } 
    &__rus {
        //top: -100%;
    }
    &__eng {
        //top: -200%;
    }
    &__info {
        //top: -300%;
    }

    &__status {
        position: absolute;
        width: 100%;
        top: -65px;
        display: flex;
        justify-content: space-between;
        span {
            padding: 0 15px;
            font-size: 3em;
            color: rgba(120, 100, 120, 0.3);          
            &.like {
                color: rgba(255, 170, 0, 0.5);                
            }
            &.memo {
                color: rgba(73, 255, 24, 0.5);
            }
        }
    }
}

